/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import Link from 'next/link';
import { useState, useEffect } from 'react';
import { Calendar, Search, MapPin, Clock, Users, Tag, Globe, Filter, ChevronDown } from 'lucide-react';

// Event interface matching your API structure
interface Event {
    eventId: number;
    title: string;
    description: string;
    shortDescription?: string;
    organizerId: number;
    organizerName: string;
    venueId: number;
    venueName: string;
    venueCity: string;
    categoryId: number;
    categoryName: string;
    startDateTime: string;
    endDateTime: string;
    imageUrl?: string;
    bannerImageUrl?: string;
    status: string;
    isPublished: boolean;
    isFeatured: boolean;
    basePrice: number;
    currency: string;
    isOnline: boolean;
    onlineUrl?: string;
    ticketsSold: number;
    availableTickets: number;
    tags?: string;
}

interface Category {
    categoryId: number;
    name: string;
    description?: string;
}

const EventCard = ({ event }: { event: Event }) => {
    // Helper function to check if event spans multiple days
    const isMultiDayEvent = (startDate: string, endDate: string) => {
        if (!startDate || !endDate) return false;

        const start = new Date(startDate);
        const end = new Date(endDate);

        if (isNaN(start.getTime()) || isNaN(end.getTime())) return false;

        const startDateOnly = start.toDateString();
        const endDateOnly = end.toDateString();

        return startDateOnly !== endDateOnly;
    };

    // Helper function to format date range
    const formatDateRange = (startDate: string, endDate: string) => {
        if (!startDate) return 'Date not set';

        const start = new Date(startDate);
        if (isNaN(start.getTime())) return 'Invalid date';

        if (!endDate || !isMultiDayEvent(startDate, endDate)) {
            return start.toLocaleDateString('en-US', {
                month: 'short',
                day: 'numeric',
                year: 'numeric'
            });
        }

        const end = new Date(endDate);
        if (isNaN(end.getTime())) {
            return start.toLocaleDateString('en-US', {
                month: 'short',
                day: 'numeric',
                year: 'numeric'
            });
        }

        // Multi-day event formatting
        const startMonth = start.getMonth();
        const endMonth = end.getMonth();
        const startYear = start.getFullYear();
        const endYear = end.getFullYear();

        if (startYear === endYear) {
            if (startMonth === endMonth) {
                // Same month and year: "Jul 22-23, 2025"
                return `${start.toLocaleDateString('en-US', { month: 'short' })} ${start.getDate()}-${end.getDate()}, ${startYear}`;
            } else {
                // Different months, same year: "Jul 5 - Aug 7, 2025"
                return `${start.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })} - ${end.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}, ${startYear}`;
            }
        } else {
            // Different years: "Dec 30, 2024 - Jan 5, 2025"
            return `${start.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })} - ${end.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })}`;
        }
    };

    // Helper function to format time range
    const formatTimeRange = (startDate: string, endDate: string) => {
        if (!startDate) return 'Time not set';

        const start = new Date(startDate);
        if (isNaN(start.getTime())) return 'Invalid time';

        const startTime = start.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

        if (!endDate) {
            return startTime;
        }

        const end = new Date(endDate);
        if (isNaN(end.getTime())) {
            return startTime;
        }

        const endTime = end.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

        if (startTime !== endTime) {
            return `${startTime} - ${endTime}`;
        }

        return startTime;
    };

    // Calculate event duration in days
    const getEventDuration = () => {
        if (!event.startDateTime || !event.endDateTime || !isMultiDayEvent(event.startDateTime, event.endDateTime)) {
            return null;
        }

        const start = new Date(event.startDateTime);
        const end = new Date(event.endDateTime);

        if (isNaN(start.getTime()) || isNaN(end.getTime())) return null;

        const diffInMs = end.getTime() - start.getTime();
        const diffInDays = Math.ceil(diffInMs / (1000 * 60 * 60 * 24));

        return diffInDays > 0 ? diffInDays : null;
    };

    const duration = getEventDuration();

    return (
        <Link href={`/events/${event.eventId}`} className="block">
            <div className="bg-white rounded-lg overflow-hidden shadow-md hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1">
                {/* Event Image */}
                <div className="relative h-48 bg-gradient-to-r from-blue-400 to-purple-500">
                    {event.imageUrl || event.bannerImageUrl ? (
                        <img
                            src={event.imageUrl || event.bannerImageUrl}
                            alt={event.title}
                            className="w-full h-full object-cover"
                        />
                    ) : (
                        <div className="w-full h-full flex items-center justify-center">
                            <Calendar className="h-16 w-16 text-white opacity-50" />
                        </div>
                    )}

                    {/* Featured Badge */}
                    {event.isFeatured && (
                        <div className="absolute top-4 left-4">
                            <span className="px-2 py-1 bg-yellow-400 text-yellow-900 text-xs font-bold rounded-full">
                                FEATURED
                            </span>
                        </div>
                    )}

                    {/* Event Type Badge */}
                    <div className="absolute top-4 right-4">
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${event.isOnline
                                ? 'bg-blue-100 text-blue-800'
                                : 'bg-purple-100 text-purple-800'
                            }`}>
                            {event.isOnline ? 'Online' : 'In-Person'}
                        </span>
                    </div>

                    {/* Multi-day Duration Badge */}
                    {duration && duration > 1 && (
                        <div className="absolute bottom-4 left-4">
                            <span className="px-2 py-1 bg-green-500 text-white text-xs font-bold rounded-full">
                                {duration} DAYS
                            </span>
                        </div>
                    )}
                </div>

                {/* Event Content */}
                <div className="p-6">
                    {/* Category */}
                    <div className="flex items-center justify-between mb-3">
                        <span className="px-3 py-1 bg-gray-100 text-gray-700 text-sm rounded-full">
                            {event.categoryName}
                        </span>
                        <span className="text-lg font-bold text-green-600">
                            {event.currency} {event.basePrice?.toFixed(2) || '0.00'}
                        </span>
                    </div>

                    {/* Event Title */}
                    <h3 className="text-xl font-bold text-gray-900 mb-2 line-clamp-2 hover:text-blue-600 transition-colors">
                        {event.title}
                    </h3>

                    {/* Event Description */}
                    <p className="text-gray-600 mb-4 line-clamp-2">
                        {event.shortDescription || event.description}
                    </p>

                    {/* Event Details */}
                    <div className="space-y-2 mb-4">
                        {/* Date Range */}
                        <div className="flex items-center text-sm text-gray-600">
                            <Calendar className="h-4 w-4 mr-2 flex-shrink-0" />
                            <div className="flex flex-col">
                                <span className="font-medium">
                                    {formatDateRange(event.startDateTime, event.endDateTime)}
                                </span>
                                {duration && duration > 1 && (
                                    <span className="text-xs text-blue-600 font-medium">
                                        {duration} day event
                                    </span>
                                )}
                            </div>
                        </div>

                        {/* Time */}
                        <div className="flex items-center text-sm text-gray-600">
                            <Clock className="h-4 w-4 mr-2 flex-shrink-0" />
                            <span>{formatTimeRange(event.startDateTime, event.endDateTime)}</span>
                        </div>

                        {/* Location */}
                        <div className="flex items-center text-sm text-gray-600">
                            {event.isOnline ? (
                                <Globe className="h-4 w-4 mr-2 flex-shrink-0" />
                            ) : (
                                <MapPin className="h-4 w-4 mr-2 flex-shrink-0" />
                            )}
                            <span className="truncate">
                                {event.isOnline ? 'Virtual Event' : `${event.venueName}, ${event.venueCity}`}
                            </span>
                        </div>

                        {/* Organizer */}
                        <div className="flex items-center text-sm text-gray-600">
                            <Users className="h-4 w-4 mr-2 flex-shrink-0" />
                            <span>By {event.organizerName}</span>
                        </div>
                    </div>

                    {/* Tickets Info */}
                    <div className="flex justify-between items-center text-sm">
                        <div className="flex items-center space-x-4">
                            <span className="text-gray-600">
                                <span className="font-medium text-gray-900">{event.ticketsSold}</span> sold
                            </span>
                            <span className="text-gray-600">
                                <span className="font-medium text-gray-900">{event.availableTickets}</span> available
                            </span>
                        </div>

                        {/* Availability Status */}
                        {event.availableTickets > 0 ? (
                            <span className="text-green-600 font-medium">Available</span>
                        ) : (
                            <span className="text-red-600 font-medium">Sold Out</span>
                        )}
                    </div>

                    {/* Tags */}
                    {event.tags && (
                        <div className="mt-3 pt-3 border-t border-gray-100">
                            <div className="flex flex-wrap gap-1">
                                {event.tags.split(',').slice(0, 3).map((tag, index) => (
                                    <span key={index} className="px-2 py-1 bg-blue-50 text-blue-700 text-xs rounded">
                                        {tag.trim()}
                                    </span>
                                ))}
                                {event.tags.split(',').length > 3 && (
                                    <span className="text-xs text-gray-500">+{event.tags.split(',').length - 3} more</span>
                                )}
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </Link>
    );
};

export default function EventsPage() {
    const [events, setEvents] = useState<Event[]>([]);
    const [categories, setCategories] = useState<Category[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedCategory, setSelectedCategory] = useState<string>('');
    const [selectedType, setSelectedType] = useState<string>('');
    const [showFilters, setShowFilters] = useState(false);

    useEffect(() => {
        fetchEvents();
        fetchCategories();
    }, []);

    const fetchEvents = async () => {
        try {
            const response = await fetch('http://localhost:5251/api/events');
            if (response.ok) {
                const data = await response.json();
                // Filter only published events
                const publishedEvents = data.filter((event: Event) => event.isPublished);
                setEvents(publishedEvents);
                console.log('Loaded published events:', publishedEvents);
            }
        } catch (error) {
            console.error('Error fetching events:', error);
        } finally {
            setLoading(false);
        }
    };

    const fetchCategories = async () => {
        try {
            const response = await fetch('http://localhost:5251/api/categories');
            if (response.ok) {
                const data = await response.json();
                setCategories(data);
            }
        } catch (error) {
            console.error('Error fetching categories:', error);
        }
    };

    const handleSearch = () => {
        // Implement search functionality
        console.log('Searching for:', searchTerm);
    };

    const filteredEvents = events.filter(event => {
        const matchesSearch = searchTerm === '' ||
            event.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
            event.description.toLowerCase().includes(searchTerm.toLowerCase());

        const matchesCategory = selectedCategory === '' ||
            event.categoryId.toString() === selectedCategory;

        const matchesType = selectedType === '' ||
            (selectedType === 'online' && event.isOnline) ||
            (selectedType === 'in-person' && !event.isOnline);

        return matchesSearch && matchesCategory && matchesType;
    });

    // Separate featured and regular events
    const featuredEvents = filteredEvents.filter(event => event.isFeatured);
    const regularEvents = filteredEvents.filter(event => !event.isFeatured);

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Hero Section */}
            <div className="bg-gradient-to-br from-blue-600 to-blue-800">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-20 pb-16">
                    <div className="text-center">
                        <h1 className="text-4xl md:text-6xl font-bold text-white mb-6">
                            Discover Amazing Events
                        </h1>
                        <p className="text-xl md:text-2xl text-blue-100 mb-8 max-w-3xl mx-auto">
                            Find and book tickets for concerts, conferences, workshops, and more in your area.
                        </p>

                        {/* Search Bar */}
                        <div className="flex flex-col sm:flex-row gap-4 justify-center max-w-2xl mx-auto">
                            <div className="relative flex-1">
                                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
                                <input
                                    type="text"
                                    placeholder="Search events..."
                                    value={searchTerm}
                                    onChange={(e) => setSearchTerm(e.target.value)}
                                    className="w-full pl-10 pr-4 py-3 rounded-lg text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-400"
                                    onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
                                />
                            </div>
                            <button
                                onClick={handleSearch}
                                className="bg-blue-500 hover:bg-blue-400 text-white font-semibold py-3 px-8 rounded-lg text-lg transition-colors duration-200"
                            >
                                Search Events
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            {/* Filters */}
            <div className="bg-white border-b border-gray-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className="flex items-center space-x-4">
                            <button
                                onClick={() => setShowFilters(!showFilters)}
                                className="flex items-center space-x-2 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
                            >
                                <Filter className="h-4 w-4" />
                                <span>Filters</span>
                                <ChevronDown className={`h-4 w-4 transition-transform ${showFilters ? 'rotate-180' : ''}`} />
                            </button>

                            {(selectedCategory || selectedType) && (
                                <button
                                    onClick={() => {
                                        setSelectedCategory('');
                                        setSelectedType('');
                                    }}
                                    className="text-sm text-blue-600 hover:text-blue-800"
                                >
                                    Clear filters
                                </button>
                            )}
                        </div>

                        <div className="text-sm text-gray-600">
                            {filteredEvents.length} events found
                        </div>
                    </div>

                    {/* Filter Options */}
                    {showFilters && (
                        <div className="mt-4 pt-4 border-t border-gray-200">
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Category</label>
                                    <select
                                        value={selectedCategory}
                                        onChange={(e) => setSelectedCategory(e.target.value)}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                    >
                                        <option value="">All Categories</option>
                                        {categories.map(category => (
                                            <option key={category.categoryId} value={category.categoryId}>
                                                {category.name}
                                            </option>
                                        ))}
                                    </select>
                                </div>

                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Event Type</label>
                                    <select
                                        value={selectedType}
                                        onChange={(e) => setSelectedType(e.target.value)}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                    >
                                        <option value="">All Types</option>
                                        <option value="online">Online Events</option>
                                        <option value="in-person">In-Person Events</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>

            {/* Main Content */}
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                {loading ? (
                    <div className="flex items-center justify-center py-20">
                        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
                    </div>
                ) : (
                    <>
                        {/* Featured Events */}
                        {featuredEvents.length > 0 && (
                            <div className="mb-12">
                                <h2 className="text-2xl md:text-3xl font-bold text-gray-900 mb-8">Featured Events</h2>
                                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                                    {featuredEvents.map((event) => (
                                        <EventCard key={event.eventId} event={event} />
                                    ))}
                                </div>
                            </div>
                        )}

                        {/* All Events */}
                        <div>
                            <h2 className="text-2xl md:text-3xl font-bold text-gray-900 mb-8">
                                {featuredEvents.length > 0 ? 'All Events' : 'Events'}
                            </h2>

                            {regularEvents.length === 0 ? (
                                <div className="text-center py-20">
                                    <Calendar className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                                    <h3 className="text-xl font-semibold text-gray-900 mb-2">No events found</h3>
                                    <p className="text-gray-600">Try adjusting your search or filters.</p>
                                </div>
                            ) : (
                                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                                    {regularEvents.map((event) => (
                                        <EventCard key={event.eventId} event={event} />
                                    ))}
                                </div>
                            )}
                        </div>
                    </>
                )}
            </div>

            {/* CTA Section */}
            <div className="bg-gray-900 py-16 mt-16">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
                    <h2 className="text-3xl md:text-4xl font-bold text-white mb-4">
                        Never Miss an Event Again
                    </h2>
                    <p className="text-xl text-gray-300 mb-8 max-w-2xl mx-auto">
                        Create an account to save your favorite events and get personalized recommendations.
                    </p>
                    <Link href="/register" className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-8 rounded-lg text-lg transition-colors duration-200">
                        Sign Up Now
                    </Link>
                </div>
            </div>
        </div>
    );
}